.inquiry-grid{

    height : 100vh;
    display: grid;
    grid-template-columns: 260px 1fr;

}

.content {
    color: var(--text-color);
    margin: 30px;
    text-align: center;
    display: grid;
    grid-template-rows: 100fr 1fr;
    overflow-y: auto;
}


.table input{
    border : var(--border);
    padding: 8px;
    text-align: left;
    background-color: var(--bg);
    color: var(--text-color);
}

.page {
    text-align: initial;
    margin-bottom: 30px;
    padding-left: 30px;
    padding-top: 30px;
}

.pagePN{
    display: flex;
    text-align: center;
    justify-content: center;
}
.pagePN .button{
    background-color: var(--background);
    width: 50px;
    font-size: 14px;;
    border : var(--border);
}

.pagePN .button:hover{
    background-color: var(--sidebar-accent);
}

.pagePN .select{
    width: 100px;
}

.movepage{
    margin-left: 30px;
    margin-right: 30px;
}
.btn-grid{
    margin-left: 30px;
    margin-right: 30px;
    display: flex;
    justify-content: center;
}

.name .search {
    width: 100px;
}

.age .numRangePrevious {
    width: 70px;
}

.age .numRangeNext {
    width: 70px;
}

.gender .select{
    background-color: var(--bg);
    color: var(--text-color);
    width:88px;
    height: 33px;
}

.address .search {
    width: 300px;
}

.phonenum .search {
    width: 120px;
}

.medical-records .search {
    width: 120px;
}

.protector .search {
    width: 100px;
}

.pphonenum .search {
    width: 120px;
}

.PIC .search {
    width:100px;
}

.sensor-name .search {
    width: 150px;
}

.connected .search {
    width: 100px;
}

.battery-status .numRangePrevious{
    width: 70px;
}

.battery-status .numRangeNext{
    width: 70px;
}

.battery-change .daterange-previous {
    width: 120px;
}

.battery-change .daterange-next {
    width: 120px;
}

.change-count .search {
    width: 70px;
}

.powerstatus .select{
    width: 88px;
    height: 33px;
    background-color: var(--bg);
    color : var(--text-color);

}

.networkstatus .select {
    width: 130px;
    height: 33px;
    background-color: var(--bg);
    color : var(--text-color);
}

.name-link:hover {
    background-color: var(--sidebar-accent);
}