


.container{
    width: 100vw;
    height : 100vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 7rem;
    color :white;
}

.container *{
    padding: 0;
    margin : 0;
    box-sizing: border-box;
    
}

.img {
    display:flex;
    justify-content: flex-end;
    align-items:center;
}
.img img{
    display: block;
    width:400px;
}

.register-link{
    display: block;
    text-align: right;
    text-decoration: none;
    color:#999;
    font-size: 0.9rem;
    transition: .3s;
}

.login-content{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
}

form {
    width : 360px;
}

.avatar{
    height: 100px;
}

form h2{
    font-size: 2.9rem;
    text-transform: uppercase;
    margin : 15px 0;
    color: var(--text-color);
}

.input-div{
    position: relative;
    display: grid;
    grid-template-columns: 7% 93%;
    margin: 25px 0;
    padding: 5px 0;
    border-bottom: 2px solid #d9d9d9;

}

.input-div:after, .input-div:before{
    content: '';
    position: absolute;
    bottom: -2px;
    width: 50px;
    height: 2px;
    background-color: var(--text-color);
}

.input-div:after{
    right: 50%;
}

.input-div:before{
    left: 50%;
}

.input-div.focus:before, .input-div.focus:after{
	width: 50%;
    transition : .3s;
}
.input-div.focus .i i{
    color: var(--sidebar-accent-accent);
}

.input-div.focus div h5{
    top: -5px;
    font-size: 15px;
}

.input-div.one{
    margin-top:0;
}
.input-div.two{
    margin-bottom : 4px;
}

.i {
    display: flex;
    justify-content: center;
    align-items: center;
}

.i i{
    color:#d9d9d9;
}

.input-div > div{
    position: relative;
    height: 45px;
    display: flex;
}

.input-div > div > h5{
    position: absolute;
    left:10px;
    top:50%;
    transform: translateY(-50%);
    color:#999;
    font-size: 18px;
    transition: .3s;
}

.input{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border: none;
    outline: none;
    background: none;
    padding: 0.5rem 0.7rem;
    font-size: 1.2rem;
    color: #555;
}

.login-link{
    display: block;
    text-align: right;
    text-decoration: none;
    color:#999;
    font-size: 0.9rem;
    transition: .3s;
}

.login-link:hover{
    color: #d4d4d4;
}

.btn{
    display:block;
    width: 100%;
    height: 50px;
    border-radius: 25px;
    margin: 1rem 0;
    font-size: 1.2rem;
    outline: none;
    border: none;
    background-color: var(--sidebar-accent);
    color: var(--text-color);
    background-size: 200%;
    text-transform: uppercase;
    transition: .5s;
    text-align: center;
}

.btn:hover{
    background-position : center;
    background-color: var(--sidebar-accent-accent);
}


@media screen and (max-width: 1050px){
	.container{
		grid-gap: 5rem;
	}
}

@media screen and (max-width: 1000px){
	form{
		width: 290px;
	}

	.login-content h2{
        font-size: 2.4rem;
        margin: 8px 0;
	}

	.img img{
		width: 400px;
	}
}

@media screen and (max-width: 950px){
	.container{
		grid-template-columns: 1fr;
	}

	.img{
		display: none;
	}

	.login-content{
		justify-content: center;
	}
}